// src/pages/hr/jobs.js
import { useState, useEffect } from "react";
import { supabase } from "lib/supabase";
import { useRouter } from "next/router";
import Image from "next/image";
import toast, { Toaster } from "react-hot-toast";
import { Icon } from "@iconify/react";

export default function HRJobBoard() {
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [file, setFile] = useState(null);
    const [expiresOn, setExpiresOn] = useState("");
    const [jobs, setJobs] = useState([]);
    const router = useRouter();

    useEffect(() => {
        if (!localStorage.getItem("hr_session")) {
            router.push("/hr/login");
        } else {
            fetchJobs();
        }
    }, [router]);

    const fetchJobs = async () => {
        const { data, error } = await supabase.from("job_openings").select("*").order("created_at", { ascending: false });
        if (error) {
            console.error("Error fetching jobs:", error);
            toast.error("Failed to load job openings.");
        } else {
            setJobs(data);
        }
    };

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        let fileUrl = null;

        if (file) {
            fileUrl = "https://drive.google.com/example"; // Placeholder, replace with driveUtils.js logic
        }

        const { error } = await supabase.from("job_openings").insert([
            {
                title,
                description: description || null,
                file_url: fileUrl || null,
                expires_on: expiresOn,
            },
        ]);

        if (error) {
            console.error("Error submitting job:", error);
            toast.error("Failed to submit job opening.");
        } else {
            toast.success("Job opening added successfully!");
            setTitle("");
            setDescription("");
            setFile(null);
            setExpiresOn("");
            fetchJobs();
        }
    };

    const handleLogout = () => {
        localStorage.removeItem("hr_session");
        toast.success("Logged out successfully!");
        setTimeout(() => router.push("/hr/login"), 1000);
    };

    return (
        <div className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100 flex flex-col">
            <Toaster position="top-right" reverseOrder={false} />
            {/* Header */}
            <header className="bg-white shadow-md p-4 flex justify-between items-center">
                <div className="flex items-center">
                    <Image
                        src="/assets/images/logo-tagline-orange.svg"
                        alt="Growthpad Consulting Group Logo"
                        width={150}
                        height={50}
                        className="mr-4"
                    />
                    <h1 className="text-2xl font-bold text-[#231812]">HR Job Board</h1>
                </div>
                <button
                    onClick={handleLogout}
                    className="flex items-center px-4 py-2 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200"
                >
                    <Icon icon="mdi:logout" width={20} height={20} className="mr-2" />
                    Logout
                </button>
            </header>

            {/* Main Content */}
            <div className="flex flex-1 p-6 gap-6 max-w-7xl mx-auto">
                {/* Form Sidebar */}
                <div className="w-full md:w-1/3 bg-white p-6 rounded-lg shadow-lg animate-fade-in">
                    <h2 className="text-xl font-semibold text-[#231812] mb-4">Add New Job Opening</h2>
                    <form onSubmit={handleSubmit}>
                        <div className="mb-4">
                            <label className="block text-sm font-medium text-[#231812] mb-1">
                                Job Title <span className="text-red-500">*</span>
                            </label>
                            <input
                                type="text"
                                value={title}
                                onChange={(e) => setTitle(e.target.value)}
                                className="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200"
                                required
                            />
                        </div>
                        <div className="mb-4">
                            <label className="block text-sm font-medium text-[#231812] mb-1">Description (Optional)</label>
                            <textarea
                                value={description}
                                onChange={(e) => setDescription(e.target.value)}
                                className="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200"
                                rows="4"
                            />
                        </div>
                        <div className="mb-4">
                            <label className="block text-sm font-medium text-[#231812] mb-1">
                                Upload File (PDF/DOCX, Optional)
                            </label>
                            <input
                                type="file"
                                accept=".pdf,.docx"
                                onChange={handleFileChange}
                                className="w-full p-2 border rounded-lg text-gray-600"
                            />
                        </div>
                        <div className="mb-6">
                            <label className="block text-sm font-medium text-[#231812] mb-1">
                                Expires On <span className="text-red-500">*</span>
                            </label>
                            <input
                                type="date"
                                value={expiresOn}
                                onChange={(e) => setExpiresOn(e.target.value)}
                                className="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] transition duration-200"
                                required
                            />
                        </div>
                        <button
                            type="submit"
                            className="w-full p-2 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 shadow-md"
                        >
                            Submit Job
                        </button>
                    </form>
                </div>

                {/* Job Listings */}
                <div className="w-full md:w-2/3">
                    <h3 className="text-xl font-bold text-[#231812] mb-4">Current Job Openings</h3>
                    {jobs.length === 0 ? (
                        <p className="text-gray-500 italic">No job openings yet.</p>
                    ) : (
                        <div className="space-y-4">
                            {jobs.map((job) => (
                                <div
                                    key={job.id}
                                    className="bg-white p-4 rounded-lg shadow-md hover:shadow-lg transition duration-200 animate-fade-in"
                                >
                                    <h4 className="text-lg font-semibold text-[#231812]">{job.title}</h4>
                                    {job.description && (
                                        <p className="text-gray-700 mt-1">{job.description}</p>
                                    )}
                                    {job.file_url && (
                                        <a
                                            href={job.file_url}
                                            className="text-[#f05d23] underline hover:text-[#d94f1e] mt-1 inline-block"
                                            target="_blank"
                                            rel="noopener noreferrer"
                                        >
                                            View File
                                        </a>
                                    )}
                                    <p className="text-sm text-gray-500 mt-2">
                                        Expires: {new Date(job.expires_on).toLocaleDateString()}
                                    </p>
                                </div>
                            ))}
                        </div>
                    )}
                </div>
            </div>

            {/* Footer */}
            <footer className="bg-white p-4 text-center text-gray-500 text-sm shadow-inner">
                &copy; {new Date().getFullYear()} Growthpad Consulting Group. All rights reserved.
            </footer>
        </div>
    );
}